/* src/components/TableDisplay.css */
.table-display-container {
    width: 100%;
    overflow-x: auto; /* Оставляем горизонтальную прокрутку, если таблица всё же слишком широкая */
    max-width: 100%; /* Убедимся, что контейнер не выходит за пределы родителя */
}

.table-display {
    width: 100%; /* Таблица занимает всю доступную ширину контейнера */
    border-collapse: collapse;
    font-size: 0.625vw; /* Оставляем шрифт, но он может адаптироваться */
    table-layout: auto; /* Позволяет ячейкам сжиматься и растягиваться */
}

.table-display th,
.table-display td {
    padding: 0.5vh 0.5vw;
    border: 0.05vw solid #dee2e6;
    text-align: left;
    white-space: normal; /* Позволяем тексту переноситься */
    word-wrap: break-word; /* Перенос длинных слов */
    min-width: 5vw; /* Минимальная ширина столбца, чтобы избежать слишком узких столбцов */
    max-width: 15vw; /* Ограничиваем максимальную ширину столбца */
    box-sizing: border-box; /* Учитываем padding и border в ширине */
}

.table-display th {
    background-color: #f8f9fa;
    font-weight: bold;
}

.notification {
    padding: 0.5vh 1vw;
    margin-bottom: 1vh;
    border-radius: 0.3vw;
    font-size: 0.625vw;
    text-align: center;
}

.notification.success {
    background-color: #d4edda;
    color: #155724;
}

.notification.error {
    background-color: #f8d7da;
    color: #b22633;
}